.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #1665b2;
  background-color: #f4f8fe;
  font-family: "Open Sans";
}

h1,
h2 {
  font-family: "Montserrat";
}

/* for table label and arrow icon, override MUI style */
.MuiTableSortLabel-root.MuiTableSortLabel-active,
.MuiTableSortLabel-root.MuiTableSortLabel-active.MuiTableSortLabel-root.MuiTableSortLabel-active
  .MuiTableSortLabel-icon {
  color: #1665b2 !important;
}
.MuiTableSortLabel-root:hover {
  color: #1665b28c !important;
}

/* HEADER */
.header {
  display: flex;
  width: 99vw;
  height: 10vh;
  h1 {
    font-size: 1.5em;
    align-self: center;
  }
  .menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 74vw;
    font-size: 1em;
    .nav {
      display: flex;
      width: 54%;
      justify-content: space-evenly;
    }
    a {
      text-transform: initial;
      margin: 0 2px;
      color: white;
      border-radius: 15px;
      color: white;
      border: 0.1px solid #45a8df;
    }
  }
}

/* page Accueil */
.accueil {
  height: 79vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  h2 {
    margin-block-end: 0;
    margin-block-start: 1.5em;
  }
}

/* page Tables */
.tables {
  width: 84vw;
  height: 90vh;
  h2 {
    font-weight: normal;
  }
}

/* page Variables */
.variables {
  h2 {
    font-weight: normal;
  }
}

/* Drawer */
//override mui style
.MuiTableRow-root:nth-child(even) {
  background-color: #fafaf9 !important;
}
